import { BadgeButton } from '@/shared/Badge'
import clsx from 'clsx'
import { FC } from 'react'

interface Props {
  className?: string
  itemClass?: string
  categories: {
    name: string
    handle: string
    id: string
  }[]
  vr: 'ar' | 'fr'
}

const CategoryBadgeList: FC<Props> = ({
  className,
  itemClass,
  categories,
  vr,
}) => {
  return (
    <div
      className={clsx(
        'category-badge-list flex flex-wrap gap-x-2 gap-y-1',
        className,
      )}
    >
      {categories.map((item, index) => {
        const link =
          vr === 'fr' ? `/news/fr/${item.handle}` : `/news/${item.handle}`

        return (
          <BadgeButton
            className={itemClass}
            key={index}
            href={link}
            color={'lime'}
          >
            {item.name}
          </BadgeButton>
        )
      })}
    </div>
  )
}

export default CategoryBadgeList
