import { useEffect, useState } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent } from '@/components/ui/card'
import { WifiOff, RefreshCw, Globe, Home } from 'lucide-react'
import { useNavigate } from '@tanstack/react-router'

interface NoInternetProps {
  onRetry?: () => void
  showRetryButton?: boolean
  title?: string
  message?: string
  checkConnection?: () => Promise<boolean>
  isOnline: boolean
  isFr: boolean
}

const NoInternet = ({
  onRetry,
  showRetryButton = true,
  title = 'No Internet Connection',
  message = 'Please check your internet connection to continue reading the latest news.',
  checkConnection,
  isFr,
}: NoInternetProps) => {
  const [isChecking, setIsChecking] = useState(false)
  const navigate = useNavigate()

  const texts = {
    title: isFr ? 'Pas de connexion Internet' : 'لا يوجد اتصال بالإنترنت',
    message: isFr
      ? 'Veuillez vérifier votre connexion Internet pour continuer à lire les dernières actualités.'
      : 'يرجى التحقق من اتصالك بالإنترنت لمتابعة قراءة أحدث الأخبار.',
    tryAgain: isFr ? 'Réessayer' : 'حاول مرة أخرى',
    goHome: isFr ? "Aller à l'accueil" : 'العودة للصفحة الرئيسية',
    checking: isFr
      ? 'Vérification de la connexion...'
      : 'جارٍ التحقق من الاتصال...',
    autoRestore: isFr
      ? "La connexion sera rétablie automatiquement lorsqu'elle sera disponible"
      : 'سيتم استعادة الاتصال تلقائيًا عند توفره',
  }

  const handleRetry = async () => {
    setIsChecking(true)
    try {
      if (checkConnection) {
        const connected = await checkConnection()
        if (connected && onRetry) {
          onRetry()
        }
      } else if (onRetry) {
        onRetry()
      }
    } finally {
      setTimeout(() => setIsChecking(false), 1000)
    }
  }

  const handleGoHome = () => {
    navigate({ to: isFr ? '/news/fr' : '/news' })
  }

  useEffect(() => {
    let interval: NodeJS.Timeout | undefined
    // if (!isOnline) {
    interval = setInterval(() => {
      handleRetry()
    }, 10000) // retry every 5s
    // }
    return () => clearInterval(interval)
  }, [])

  return (
    <div className=" z-40 flex items-center justify-center bg-background/80 backdrop-blur-sm">
      <Card className="mx-4 w-full max-w-md border-offline/20 bg-offline-accent/5">
        <CardContent className="flex flex-col items-center space-y-6 p-8 text-center">
          <div className="relative">
            <div className="rounded-full bg-lime-200 p-6">
              <WifiOff className="h-12 w-12 text-offline animate-pulse" />
            </div>
            <div className="absolute -bottom-1 -right-1 rounded-full bg-black/80 p-2">
              <Globe className="h-4 w-4 text-white" />
            </div>
          </div>

          <div className="space-y-2">
            <h2 className="text-2xl font-bold text-foreground">
              {texts.title}
            </h2>
            <p className="text-muted-foreground leading-relaxed">
              {texts.message}
            </p>
          </div>

          {showRetryButton && (
            <Button
              onClick={handleRetry}
              disabled={isChecking}
              variant="outline"
              size="lg"
              className="w-full border-offline/30 text-offline hover:bg-lime-600 hover:text-white cursor-pointer"
            >
              {isChecking ? (
                <>
                  <RefreshCw className="mr-2 h-4 w-4 animate-spin" />
                  {texts.checking}
                </>
              ) : (
                <>
                  <RefreshCw className="mr-2 h-4 w-4" />
                  {texts.tryAgain}
                </>
              )}
            </Button>
          )}

          <Button
            onClick={handleGoHome}
            disabled={isChecking}
            variant="outline"
            size="lg"
            className="w-full border-offline/30 text-offline hover:bg-lime-600 hover:text-white cursor-pointer -mt-4"
          >
            <Home className="mr-2 h-4 w-4 " />
            {texts.goHome}
          </Button>

          <div className="text-sm text-muted-foreground">
            {texts.autoRestore}{' '}
          </div>
        </CardContent>
      </Card>
    </div>
  )
}

export default NoInternet
