import { shuffle } from '@/config'
import { originToUsable, TUnPost } from '@/data/posts'
import { TPost } from '@/types'
import { useQuery } from '@tanstack/react-query'

const api = import.meta.env.VITE_API_NEWS

export const useGetTrending = (
  time?: '1h' | '24h' | '6h' | '7j' | '30j',
  cat?: string,
  rand?: boolean,
  number?: number,
) => {
  const query = useQuery({
    enabled: !!time,
    staleTime: 1000 * 600,
    queryKey: ['trend', { time, cat, rand: !!rand }],
    queryFn: async () => {
      const response = await fetch(`${api}/trending/${time}?cat=${cat}`)

      if (!response.ok) {
        throw new Error('faild to fetch posts')
      }
      const { results: unformatedPosts }: trendingResponse =
        await response.json()

      const beforeRandPosts: TPost[] = !!number
        ? unformatedPosts.slice(0, number).map((post) => originToUsable(post))
        : unformatedPosts.map((post) => originToUsable(post))

      const posts = rand ? shuffle(beforeRandPosts) : beforeRandPosts

      // const posts = beforeRandPosts
      return { posts }
    },
  })
  return query
}

export interface trendingResponse {
  categorie: string
  pageSize: string
  total: number
  results: TUnPost[]
}
