import Heading from '@/shared/Heading'
import { TPost } from '@/types'
import Card17Podcast from './PostCards/Card17Podcast'
import clsx from 'clsx'
import { ChevronRight, ChevronLeft } from 'lucide-react'
import { Link } from '@tanstack/react-router'
// import GoogleAd from './GoogleAd'

const Weekly = ({
  hedding = 'LES PLUS POPULAIRES CETTE SEMAINE',
  posts,
  full = false,
  vr,
  loading,
}: {
  hedding: string
  posts: TPost[]
  full?: boolean
  vr: 'ar' | 'fr'
  loading?: boolean
}) => {
  const path = vr === 'fr' ? '/news/fr/$postId' : '/news/$postId'

  if (loading)
    return (
      <div className="pt-4">
        <div className="space-y-6">
          <div className="h-6 w-48 rounded bg-gray-300"></div>
          {Array.from({ length: 8 }).map((_, i) => (
            <div key={i} className="flex gap-3">
              <div className="h-20 w-20 rounded-full bg-gray-300"></div>
              <div className="flex-1 space-y-1 mt-4">
                <div className="h-4 w-full rounded bg-gray-200"></div>
                <div className="h-4 w-2/3 rounded bg-gray-200"></div>
              </div>
            </div>
          ))}
        </div>
      </div>
    )

  return (
    <div
      className={clsx(
        'w-full space-y-7 ',
        !full && 'lg:w-2/5 lg:ps-10  xl:w-1/3 xl:ps-0',
      )}
    >
      <div>
        <Heading level={2}>
          <Link
            aria-label={hedding}
            to={path}
            params={{ postId: 'trending-7days' }}
            className="flex items-center space-x-4  "
          >
            {vr !== 'fr' && <span></span>}
            {hedding}
            {/*{vr === 'ar'
              ? 'الأكثر رواجًا هذا الاسبوع'
              : 'Trending, les plus populaires des denières '}*/}{' '}
            {vr === 'fr' ? (
              <ChevronRight size={19} strokeWidth={3} className="mt-1 mx-1" />
            ) : (
              <ChevronLeft size={19} strokeWidth={3} className="mt-1 mx-1" />
            )}{' '}
          </Link>
        </Heading>
        {/*<GoogleAd slot="1234567890" className="my-4" />*/}

        <div className="flex flex-col space-y-6 sm:col-span-6 lg:col-span-2">
          {posts.map((p, _) => (
            <Card17Podcast key={p.id} post={p} vr={vr} />
          ))}
        </div>
      </div>
    </div>
  )
}

export default Weekly
