'use client'

import { Bookmark02Icon } from '@hugeicons/core-free-icons'
import { HugeiconsIcon } from '@hugeicons/react'
// import { useQueryClient } from '@tanstack/react-query'
import clsx from 'clsx'
import { FC, useEffect, useState } from 'react'

interface Props {
  className?: string
  bookmarked?: boolean
  handle: string
  vr: 'ar' | 'fr'
}

const BookmarkBtn: FC<Props> = ({ className, handle, vr }) => {
  const [isBookmarked, setIsBookmarked] = useState(isPostSaved(handle))

  const favLink =
    'https://ana.fibladi.com/fr/ExtFavorites/type/News?url=' + `${handle}`
  // const queryClient = useQueryClient()

  //todo
  // useEffect(() => setIsBookmarked(isPostSaved(handle)), [])

  const toggleSave = () => {
    if (isBookmarked) {
      removePost(handle)
    } else {
      window.open(favLink, '_blank') // open in new tab

      savePost(handle)
    }
    setIsBookmarked(!isBookmarked)
    // queryClient.invalidateQueries({ queryKey: ['saved'] })
  }

  return (
    <button
      key={handle}
      className={clsx(
        'relative flex size-8 items-center justify-center rounded-full bg-neutral-50 transition-colors duration-300 hover:bg-neutral-100 dark:bg-white/10 dark:hover:bg-white/20  cursor-pointer',
        className,
      )}
      title="Save to reading list"
      onClick={toggleSave}
      type="button"
    >
      <HugeiconsIcon
        icon={Bookmark02Icon}
        size={18}
        strokeWidth={1.75}
        fill={isBookmarked ? 'currentColor' : 'none'}
      />
    </button>
  )
}

function savePost(postId: string) {
  // Get the current saved posts from localStorage
  const savedPosts = JSON.parse(localStorage.getItem('savedPosts') || '[]')

  // Avoid duplicates
  if (!savedPosts.includes(postId)) {
    savedPosts.push(postId)
    localStorage.setItem('savedPosts', JSON.stringify(savedPosts))
  }
}

function removePost(postId: string) {
  const savedPosts = JSON.parse(localStorage.getItem('savedPosts') || '[]')
  const filteredPosts = savedPosts.filter((id: string) => id !== postId)
  localStorage.setItem('savedPosts', JSON.stringify(filteredPosts))
}

function isPostSaved(postId: string): boolean {
  const savedPosts = JSON.parse(localStorage.getItem('savedPosts') || '[]')
  return savedPosts.includes(postId)
}

// function getSavedPosts(): string[] {
//   return JSON.parse(localStorage.getItem('savedPosts') || '[]')
// }

export default BookmarkBtn
