import { shuffle } from '@/config'
import { originToUsable, TUnPost } from '@/data/posts'
import { TPost } from '@/types'
import { useQuery } from '@tanstack/react-query'

const api = import.meta.env.VITE_API_NEWS

export const useGetLastPost = (
  lang: 'fr' | 'ar',
  page?: string,
  rand?: boolean,
) => {
  const query = useQuery({
    enabled: !!lang,
    staleTime: rand ? 1000 * 60 : 1000 * 260,
    queryKey: ['last', { lang, page, rand: !!rand }],
    queryFn: async () => {
      const response = await fetch(`${api}/lastnews?lang=${lang}&p=${page}`)
      if (!response.ok) {
        throw new Error('faild to fetch posts')
      }

      const { results: unformatedPosts, total }: categorieResponse =
        await response.json()

      const beforeRandPosts: TPost[] = unformatedPosts.map((post) =>
        originToUsable(post),
      )

      const posts = rand ? shuffle(beforeRandPosts) : beforeRandPosts
      // const posts = beforeRandPosts
      // console.log(total)
      return { posts, total }
    },
  })
  return query
}

export interface categorieResponse {
  page: number
  pageSize: number
  total: number
  results: TUnPost[]
}
