'use client'

import { Link, useLocation } from '@tanstack/react-router'
import SpinLoading from '@/shared/spin-loading'
import {
  SpeakerWaveIcon,
  SpeakerXMarkIcon,
  PauseCircleIcon,
} from '@heroicons/react/24/outline'
import clsx from 'clsx'
import { FC, useEffect, useRef, useState } from 'react'
import ReactPlayer from 'react-player'
import { useIsPWA } from '@/hooks/useIsPWA'

interface Props {
  videoUrl: string
  isHover: boolean
  handle: string
  vr: 'ar' | 'fr'
  setIsHover: () => void
}

const MediaVideo: FC<Props> = ({
  videoUrl,
  isHover,
  setIsHover,
  handle,
  vr,
}) => {
  const [isMuted, setIsMuted] = useState(false)
  const [showDescUnmuted, setShowDescUnmuted] = useState(true)
  const [isPlaying, setIsPlaying] = useState(false)
  const [isRendered, setIsRendered] = useState(false)
  let __timeOut: NodeJS.Timeout | null = null
  const playerRef = useRef<ReactPlayer | null>(null)

  const path = vr === 'fr' ? '/news/fr/$postId' : '/news/$postId'
  const link = decodeURIComponent(handle)
  const location = useLocation()

  const isPWA = useIsPWA()
  useEffect(() => {
    if (isHover) {
      setIsRendered(true)
      playerRef.current?.seekTo(0, 'seconds')
    }
  }, [isHover])

  useEffect(() => {
    return () => {
      __timeOut && clearTimeout(__timeOut)
    }
  }, [__timeOut])

  return (
    <div
      className={clsx(
        'absolute inset-0 z-20',
        isHover ? 'opacity-100' : 'opacity-0',
      )}
    >
      {isRendered && (
        <ReactPlayer
          ref={playerRef}
          url={videoUrl}
          muted={isMuted}
          playing={isHover}
          style={{
            opacity: isPlaying ? 1 : 0,
          }}
          className="absolute inset-0 bg-neutral-900 transition-opacity"
          width="100%"
          height="100%"
          onStart={() => {
            setIsPlaying(true)
            __timeOut && clearTimeout(__timeOut)
            __timeOut = setTimeout(() => {
              setShowDescUnmuted(false)
            }, 2500)
          }}
        />
      )}

      <Link
        onClick={() => {
          setIsHover()
          if (
            decodeURIComponent(location.pathname) ===
            path.replace('$postId', link)
          ) {
            window.scrollTo({ top: 0, behavior: 'smooth' })
          }
        }}
        to={path}
        {...(!isPWA && {
          target: '_blank',
        })}
        params={{ postId: link }}
        className={clsx(
          'absolute inset-0 flex items-center justify-center',
          isPlaying ? 'opacity-0' : 'opacity-100',
        )}
      >
        <SpinLoading />
      </Link>

      {isPlaying && (
        <div
          className={clsx(
            'absolute end-2 bottom-2 flex h-6 items-center justify-center rounded-full bg-black/70 text-sm text-white transition-transform cursor-pointer m-2',
          )}
          onClick={setIsHover}
        >
          <PauseCircleIcon className="size-8 z-40" />
        </div>
      )}

      {isPlaying && (
        <div
          className={clsx(
            'absolute start-2 bottom-2 flex h-6 items-center justify-center rounded-full bg-black/70 text-sm text-white transition-transform cursor-pointer m-2',
            showDescUnmuted ? 'ps-1.5 pe-2' : 'w-6 hover:scale-125',
          )}
          onClick={() => setIsMuted(!isMuted)}
        >
          {isMuted ? (
            <>
              <SpeakerXMarkIcon className="size-5" />
              {showDescUnmuted && (
                <span className="ms-1 inline-block ">Click here to unmute</span>
              )}
            </>
          ) : (
            <SpeakerWaveIcon className="size-5" />
          )}
        </div>
      )}
    </div>
  )
}

export default MediaVideo
