'use client'

import { TPost } from '@/types'
import clsx from 'clsx'
import Image from '../image'
import { FC } from 'react'
import { useLocation, Link } from '@tanstack/react-router'
import { useIsPWA } from '@/hooks/useIsPWA'

interface Props {
  className?: string
  post: TPost
  vr: 'fr' | 'ar'
}

const Card17Podcast: FC<Props> = ({ className, post, vr }) => {
  const { title, handle, featuredImage, id } = post

  const path = vr === 'fr' ? '/news/fr/$postId' : '/news/$postId'
  const link = decodeURIComponent(handle)
  const location = useLocation()
  const isPWA = useIsPWA()

  return (
    <div
      className={clsx(
        `nc-Card17Podcast relative flex items-center justify-between space-x-5 rounded-full bg-neutral-100 p-2.5 transition-shadow shadow-lg dark:bg-neutral-800/30 ${className}`,
      )}
    >
      <div className="flex items-center gap-x-4">
        <Link
          aria-label={title}
          to={`${path}`}
          params={{ postId: link }}
          search={{ id: id }}
          // className="absolute inset-0"
          {...(!isPWA && {
            target: '_blank',
          })}
          onClick={() => {
            if (location.pathname === `${path}${link}`)
              window.scrollTo({ top: 0, behavior: 'smooth' })
          }}
        >
          <div className="relative size-14 shrink-0 rounded-full shadow-lg sm:size-18">
            <Image
              sizes="(max-width: 1024px) 100vw, 50vw"
              className="rounded-full object-cover"
              src={featuredImage}
              fill
              alt={title}
            />
          </div>
        </Link>

        <div className="flex grow flex-col">
          <h2 className="block font-medium sm:text-base/snug sm:font-semibold">
            <Link
              aria-label={title}
              to={`${path}`}
              params={{ postId: link }}
              search={{ id: id }}
              // className="absolute inset-0"
              onClick={() => {
                if (location.pathname === `${path}${link}`)
                  window.scrollTo({ top: 0, behavior: 'smooth' })
              }}
            >
              <span
                className="line-clamp-2"
                dangerouslySetInnerHTML={{ __html: title }}
              ></span>
            </Link>
          </h2>
        </div>
      </div>
    </div>
  )
}

export default Card17Podcast
